body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: start;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.locate {
  display: flex; }
  .locate-bottom {
    margin-top: 5rem; }

.btn-primary,
.btn-light,
.btn-dark,
.btn-gray,
.btn-danger,
.btn-green {
  display: inline-block;
  padding: 0.8rem 2rem;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  border: none;
  cursor: pointer;
  border-radius: 2%; }

.btn-primary {
  color: #333;
  background-color: #ffbc00; }

.btn-green {
  color: #333;
  background-color: #33aa00; }

.btn-gray {
  color: #333;
  background-color: #bbbbbb; }

.btn-light {
  color: #333;
  background-color: #f4f4f4; }

.btn-dark {
  color: #f4f4f4;
  background-color: #333; }

.btn-primary:hover {
  color: #333;
  background-color: #ffac00; }

.btn-gray:hover {
  color: #333;
  background-color: #cfcfcf; }

.btn-green:hover {
  color: #333;
  background-color: #339900; }

.nav-item .nav-link {
  color: #111122; }

@media (max-width: 840px) {
  #landing .landing-container .row h1 {
    font-size: 2rem; } }

@media (max-width: 640px) {
  #typewriter {
    font-size: 1rem !important;
    line-height: normal; }
  .navbar .navbar-brand {
    display: none; }
  #mobile-nav {
    width: 100%; }
    #mobile-nav .navbar-nav {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      text-align: center; }
      #mobile-nav .navbar-nav li {
        margin: 0 0.7rem; } }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

.app > div {
  padding: 3rem; }

.main-container {
  overflow: auto; }

.container {
  height: 100%;
  padding: 0;
  margin: auto;
  float: center; }
  .container .dashboard-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }

.navbar {
  max-height: 4rem; }

.bg-main {
  background: #cc5; }

.bg-header {
  background: rgba(255, 200, 0, 0.7); }

.menubar {
  position: relative;
  z-index: 999; }

#landing {
  margin: 0;
  padding: 1.5rem;
  background: rgba(13, 110, 139, 0.75);
  height: 100vh;
  position: relative;
  overflow: hidden; }

#landing:before {
  content: "";
  background: url(/static/media/layout_i1.35ae466a.jpg) no-repeat center center/cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1; }

#landing .landing-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 90%; }
  #landing .landing-container #typewriter {
    font-size: 1.2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 8rem; }

